import React from 'react';
import Card from 'react-bootstrap/esm/Card';
import '../App.css';
// import emoji from '../assets/emoji.svg';
import fotoperf from '../assets/fotoperf.jpg';

export const Presentation: React.FC = () => {
  return (
    <>
      <Card
        className="p-3"
        border="info"
        style={{
          width: '100%',
          height: '90vh',
          backgroundColor: '#282c34',
        }}
      >
        <Card.Img variant="top" src={fotoperf} width={200} height={550} />
      </Card>
    </>
  );
};

export const Description: React.FC = () => {
  return (
    <>
      <Card
        border="info"
        style={{
          width: '100%',
          height: '90vh',
        }}
      >
        <Card.Body className="Text-color">
          <Card.Title
            className="text-center font-weight-bold"
            style={{ fontSize: 'calc(100% + 1vw + 1vh)' }}
          >
            {' '}
            ABOUT{' '}
          </Card.Title>
          <Card.Text
            className="text-center lead "
            style={{ fontSize: 'calc(50% + 1vw + 1vh)' }}
          >
            {' '}
            Hi, my name's Pablo, i'm a B.S Applied and Computational Math
            student by UNICENTRO university. I've developed projects in web area
            for both Front-End with ReactJs/TypeScript and Back-End with Nodejs
            and Django, Desktop Applications with ElectronJs, C++/GUI and
            DataScience/NumericalMethods/Statics with Python and C++, and
            MobileApplications with ReactNative.{' '}
          </Card.Text>
        </Card.Body>
      </Card>
    </>
  );
};
